<template>
  <div>
    <!-- right side-menu -->
    <v-layout>
      <v-navigation-drawer
        temporary
        v-model="drawer"
        absolute
        app
      >
        <v-list class="pa-1">
          <v-list-tile avatar>
            <v-list-tile-avatar>
              <img src="https://randomuser.me/api/portraits/men/85.jpg" >
            </v-list-tile-avatar>
            <v-list-tile-content>
              <v-list-tile-title>John Leider</v-list-tile-title>
            </v-list-tile-content>
            <v-list-tile-content @click.stop="drawer = !drawer" class="arrow-left-close">
              <v-icon>fas fa-arrow-left</v-icon>
            </v-list-tile-content>
          </v-list-tile>
        </v-list>
        <v-list class="pt-0" dense>
          <v-divider></v-divider>
            <v-list-tile v-for="item in items" :key="item.title" @click="goTo(item.link)">
                <v-list-tile-action>
                  <v-icon>{{ item.icon }}</v-icon>
                </v-list-tile-action>
                <v-list-tile-content>
                  <v-list-tile-title>{{ item.title }}</v-list-tile-title>
                </v-list-tile-content>
            </v-list-tile>
          </v-list>
      </v-navigation-drawer>
    </v-layout>

    <!-- nabigation toolbar -->
    <v-layout wrap>
      <v-toolbar dark color="primary">
        <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
        <router-link to="/">  
          <v-toolbar-title class="white--text">Know what you have</v-toolbar-title>
        </router-link>
        <v-spacer></v-spacer>
        <v-btn icon>
          <v-icon>more_vert</v-icon>
        </v-btn>
      </v-toolbar>
    </v-layout>
  </div>
</template>

<script>
export default {
  data () {
    return {
      items: {
        categories: {
          icon: "fas fa-align-justify",
          title: "Categories",
          link: 'categories'
        }
      },
      drawer: false
    }
  },
  methods: {
    goTo (link) {
      this.$router.push({name: `${link}`})
    }
  }
}
</script>

<style lang="scss" scoped>

    .arrow-left-close{
    cursor: pointer;
    align-items: flex-end;
    }

    .arrow-left-close:hover {
    position: relative;
    transform: translateX(-10px);
    transition: all 0.5s ease-in-out;
    }

</style>


